$green: #00805f;
$orange: #e3ac2c;
$black: #2e2e2e;
$red: #c35a3f;
$red-link: #ab5c4e;


@include font(din300, 'DINPro-Light');
@include font(din700, 'DINPro-Bold');
@include font(dinCond, 'DINPro-CondensedBold');

@mixin din300 {
  font-family: din300, sens-serif;
  font-size: 16px;
  line-height: 1.53;
  font-weight: 300;
  font-style: normal;
  color: $black; }

@mixin din700 {
  font-family: din700, sens-serif;
  font-size: 16px;
  line-height: 1.5;
  font-weight: bold;
  font-style: normal;
  color: $black;
  @include r(1024) {
    font-size: 14px; } }

@mixin dinCond {
  font-family: dinCond, sens-serif;
  font-size: 16px;
  line-height: 1.5;
  font-weight: bold;
  font-style: normal;
  color: $black;
  @include r(1024) {
    font-size: 14px; } }

body {
  overflow-x: hidden;
  @include din300;
  @include r(1024) {
    font-size: 14px; } }
