.gallery {
  background: url("../img/gallery2.jpg") no-repeat 0 65%, url("../img/gallery3.jpg") no-repeat 100% 65%;
  padding: 35px 0 40px;
  @include r(768) {
    background: none; }

  &__item {

    &-link {
      &:hover {}
      cursor: url(../img/lupa.png), auto; } }

  &-i {
    position: relative;
    max-width: 800px;
    height: auto;
    margin: 0 auto;
    &::before {
      content: "";
      display: block;
      position: absolute;
      left: -200px;
      top: 0;
      width: 200px;
      height: 100%;
      background-image: linear-gradient(to left, white 0%, transparent 100%); }
    &::after {
      content: "";
      display: block;
      position: absolute;
      right: -200px;
      top: 0;
      width: 200px;
      height: 100%;
      background-image: linear-gradient(to right, white 0%, transparent 100%); }

    img {
      width: 100%; }

    & .slick {

      &-arrow {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 100px;
        height: 100px;
        opacity: 0.4;
        background-color: transparent;
        cursor: pointer;
        font-size: 0;
        z-index: 10;
        @include r(1024) {
          background-color: rgba(255, 255, 255, 0.8); }
        @include r(768) {
          width: 50px;
          height: 50px; }
        &:hover {
          opacity: 1; } }
      &-prev {
          left: -100px;
          @include r(1024) {
            left: -25px; }
          @include r(768) {
            left: 0px; } }
      &-next {
          right: -100px;
          @include r(1024) {
            right: -25px; }
          @include r(768) {
            right: 0px; } }
      &-prev:after,
      &-next:after {
          margin: auto;
          z-index: 5;
          content: "";
          width: 0;
          height: 0;
          border-top: 25px solid transparent;
          border-bottom: 25px solid transparent; }
      &-prev:after {
          border-right: 50px solid $green; }
      &-next:after {
          border-left: 50px solid $green; } } } }

.slick-lightbox {

  .slick-arrow {
    position: absolute;
    top: 50%;
    z-index: 50;
    font-size: 0;
    background-color: transparent; }

  .slick-prev {
    left: 20px;
    @include arr(50,50, $green, l); }

  .slick-next {
    right: 20px;
    @include arr(50,50, $green, r); } }
