.stock {
  padding-bottom: 75px;
  @include r(1024) {
    padding-bottom: 60px; }
  @include r(768) {
    padding-bottom: 30px; }

  &-i {
    position: relative;
    max-width: 800px;
    height: auto;
    margin: 0 auto;
    margin-top: 40px; }

  &__item {
    display: flex;
    flex-direction: column;
    max-width: 370px;
    height: 400px;
    padding: 0 50px;
    text-align: center;
    font-size: 24px;
    padding-top: 125px;
    background-color: $red-link;
    background-image: url("../img/stock11.png"), url("../img/stock1.png");
    background-repeat: no-repeat;
    background-position: 50% 12%, 31% 82%; }


  &__secondary {
    padding-top: 160px;
    background-color: $red-link;
    background-image: url("../img/stock22.png"), url("../img/stock2.png");
    background-repeat: no-repeat;
    background-position: 50% 18%, 18% 88%;

    & .stock__descr--bottom {
      margin-left: 130px;
      margin-top: 40px;
      @include r(354) {
        margin-top: 10px; } } }

  &__descr {
    font-size: 24px;
    color: white;
    &--upper {
      text-transform: uppercase;
      @include r(768) {
        font-size: 22px; } }
    &--price {
      @include din700;
      font-size: 48px;
      line-height: 1;
      color: white;
      @include r(1024) {
        font-size: 40px; } }
    &--bottom {
      text-align: left;
      margin-left: 110px;
      margin-top: 40px; } }


  & .slick {

    &-slide {
      text-align: center; }

    &-arrow {
      position: absolute;
      top: 55%;
      transform: translateY(-50%);
      width: 50px;
      height: 50px;
      opacity: 0.4;
      background-color: transparent;
      cursor: pointer;
      font-size: 0;
      &:hover {
        opacity: 1; } }
    &-prev {
      left: -100px;
      @include r(1024) {
        left: 0px;
        z-index: 10; } }
    &-next {
      right: -100px;
      @include r(1024) {
        right: 0px;
        z-index: 10; } }

    &-prev:after,
    &-next:after {
      margin: auto;
      z-index: 5;
      content: "";
      width: 0;
      height: 0;
      border-top: 25px solid transparent;
      border-bottom: 25px solid transparent; }
    &-prev:after {
      border-right: 50px solid $green; }
    &-next:after {
      border-left: 50px solid $green; } } }
