.main-nav {
  margin-right: 30px;
  display: flex;
  flex-direction: column;
  @include r(768) {
    display: none;
    flex-direction: column; }
  &__list {
    display: flex;
    flex-wrap: wrap;
    @include r(768) {
      flex-direction: column;
      width: 100%; } }

  &.is-active {
    display: flex;
    width: 100%;
    @include r(768) {
      position: absolute;
      top: 54px;
      left: 0; } }

  &__item {
    @include r(768) {
      width: 100%;
      margin: 0;
      text-align: center;
      border-bottom: 1px solid lightgreen;
      background-color: $green;
      &:hover {
        background-color: lightgreen; }
      a {
        display: block;
        width: 100%;
        padding: 10px;
        &:hover {
          color: white;
          border-bottom: none; }
        &.is-active {
          color: $orange; } } } }

  &__link {
    padding: 5px 8px;
    @include din300;
    color: $black;
    text-decoration: none;
    text-transform: uppercase;
    @include r(1050) {
      font-size: 14px; }
    @include r(768) {
      font-size: 12px; }
    &:hover,
    &.is-active {
      color: $green; } } }

.phone {
  margin-left: auto;
  margin-bottom: 20px;
  margin-right: 60px;
  margin-top: 10px;
  @include r(768) {
    width: 100%;
    margin: 0; }


  &__link {
    display: flex;
    margin: auto;
    @include din700;
    font-size: 24px;
    text-decoration: none;
    color: white;
    padding: 5px 40px;
    background-color: $red;
    @include r(768) {
      justify-content: center; } } }
.btn-nav {
  position: absolute;
  top: 8px;
  right: 20px;
  margin-left: auto;
  @include r(768) {
    display: block;
    width: 40px;
    height: 40px;
    background-image: url("../img/burger.svg");
    background-color: transparent;
    fill: black; } }
.btn-nav.active {
  background-image: url("../img/close.svg"); }
