.header {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  padding-bottom: 30px;
  background-color: $orange;
  z-index: 15;
  @include r(768) {
    display: block;
    padding-bottom: 0; }

  &.is-fixed {
    display: block; }

  &-i {
    display: flex;
    width: 100%;
    margin-top: auto;
    align-items: baseline;
    @include r(768) {
      flex-wrap: wrap; } }

  &__logo {
    width: 110px;
    height: 50px;
    margin-top: auto;
    margin-right: auto;
    @include r(1024) {
      margin-right: 40px; }
    @include r(768) {
      margin-top: 15px;
      width: 60px;
      height: auto;
      margin-bottom: 10px; } }


  &__content {
    display: flex;
    flex-direction: column;
    @include r(768) {
      margin-right: auto; } } }
