.planning {
  min-height: 710px;
  padding-top: 50px;
  padding-bottom: 40px; }

.apartments {
  position: relative;
  width: 100%;
  margin-top: 50px;
  @include r(768) {
    margin-top: 10px; }

  & > div > div {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    z-index: -1;
    &.is-active {
      position: static;
      opacity: 1;
      z-index: 10; } }

  &__list {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    @include r(768) {
      flex-direction: column;
      margin-bottom: 0; } }

  &__content {
    position: relative;
    padding: 20px 65px;
    @include r(768) {
      padding: 0; } }

  &__footer {
    display: flex;
    margin-top: 45px;
    @include r(870) {
      margin-top: 0;
      flex-direction: column; } }

  &__btn {
    flex-grow: 1;
    margin-right: 35px;
    padding: 24px;
    text-align: center;
    font-size: 20px;
    @include r(1024) {
      font-size: 16px;
      padding: 15px; }
    @include r(768) {
      min-width: 250px;
      margin-right: 0;
      margin-top: 10px; } } }


.availability {
  min-width: 320px;
  margin-top: 10px;
  padding-right: 15px;
  text-align: center;
  @include r(870) {
    min-width: 190px;
    padding-right: 0; }

  &__title {
    text-transform: uppercase;
    font-size: 20px;
    color: $green;
    @include r(1024);
    font-size: 18px; }

  &__links {}

  &__link {
    margin-right: 10px;
    color: $red-link;
    text-decoration: none;
    border-bottom: 1px solid $red-link; } }


.apartment {
  min-width: 235px;
  text-align: center;
  border-bottom: 1px solid $green;
  padding: 24px;
  @include din300;
  font-size: 20px;
  text-transform: uppercase;
  cursor: pointer;
  @include r(1024) {
    min-width: 160px;
    font-size: 18px;
    padding: 20px; }
  @include r(768) {
    padding: 5px; }
  &.is-active {
    color: white;
    background-color: $green; } }


.rooms {
  margin: 0 auto;
  margin-bottom: 30px;
  @include r(768) {
    padding: 20px 65px; }

  & .slick {
    position: relative;

    &-arrow {
      position: absolute;
      top: 40%;
      transform: translateY(-50%);
      width: 50px;
      height: 50px;
      opacity: 0.4;
      background-color: transparent;
      cursor: pointer;
      font-size: 0;
      &:hover {
        opacity: 1; } }
    &-prev {
      left: -65px;
      @include r(768) {
        left: -25px; } }
    &-next {
        right: -65px;
        @include r(768) {
          right: -25px; } }
    &-prev:after,
    &-next:after {
        margin: auto;
        z-index: 5;
        content: "";
        width: 0;
        height: 0;
        border-top: 25px solid transparent;
        border-bottom: 25px solid transparent; }
    &-prev:after {
        border-right: 50px solid $green; }
    &-next:after {
        border-left: 50px solid $green; } } }

.room {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 1px;
  align-items: center;
  text-decoration: none;
  border: 1px solid transparent;
  &:hover {
    border: 1px solid $orange;
    & .room__hover {
      display: block; } }

  &__hover {
    display: none;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 25px;
    text-align: center;
    font-size: 20px;
    text-transform: uppercase;
    color: black;
    background-color: $orange;
    @include r(768) {
      font-size: 16px;
      padding: 20px; } }

  &__img {
    img {
      width: calc(100% - 5px); } }


  &__content {
    display: flex;
    flex-direction: column;
    align-items: center; }

  &__descr {
    text-align: center; }

  &__title {
    color: black; }

  &__text {
    color: black;
    &--green {
      color: $green; } }

  &__link-text {
    width: 80px;
    margin: 0 auto;
    color: $red-link;
    text-align: center;
    text-decoration: none;
    border-bottom: 1px solid $red-link; } }
