.form-popup {
  position: fixed;
  top: 200px;
  left: calc(50% - 240px);
  transform: translate-X(-50%);
  flex-direction: column;
  max-width: 480px;
  min-height: 350px;
  padding: 70px 60px 50px;
  border: 1px solid #bc6268;
  background-color: white;
  z-index: 51;

  display: none;

  &.is-active {
    display: flex; }


  input {
    width: 100%; } }
