#map {
  height: 600px; }
.map {
  position: relative;
  min-height: 600px;
  background-color: #e9e5dc;

  &__title {
    margin-bottom: 0px; }

  &__descr {
    position: absolute;
    bottom: 55px;
    width: 100%;
    min-height: 170px;
    padding: 45px;
    background-color: $green;
    text-align: center;
    color: white;
    @include r(1024) {
      min-height: 140px;
      padding: 30px; } } }
