.progress {
  padding-bottom: 55px;
  background-position: 0 44%, 100% 44%;

  &__date {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 7px;

    &-years {
      display: flex;
      a {
        font-size: 30px;
        color: $green; } }

    &-year {
      text-decoration: none;
      margin-right: 20px;
      &--under {
        text-decoration: underline; } }

    &-months {
      position: relative;
      display: flex;
      justify-content: left;
      min-width: 570px;
      margin-top: 10px;
      @include r(768) {
        flex-wrap: wrap;
        min-width: 270px;
        margin-top: 20px;
        margin-left: 20px;
        margin-right: 10px; }

      &::before {
        position: absolute;
        top: 2px;
        left: -60px;
        content: "месяц: ";
        color: $orange;
        @include r(768) {
          top: -23px;
          left: 10px; } } }

    &-month {
      position: relative;
      width: 30px;
      height: 30px;
      margin: 8px;
      line-height: 1.8;
      text-align: center;
      vertical-align: middle;
      border-radius: 50%;
      border: 1px solid $green;

      &:nth-child(-n+4) {
        &::before {
          position: absolute;
          top: 14px;
          right: -13px;
          content: "";
          display: block;
          width: 10px;
          height: 1px;
          background: $green; } }

      &:nth-child(4) {
        &::after {
          position: absolute;
          top: 11px;
          right: -14px;
          content: "";
          display: block;
          @include arr(5,7, $green, r); } }


      &.is-filled {
        color: white;
        background-color: $green; }

      &.is-active {
        color: white;
        background-color: $orange;
        border: none; } } } }

