.about-us {
  display: flex;
  flex-direction: column;
  max-width: 1050px;
  padding: 65px 0 80px;
  margin: 0 auto;
  @include r(768) {
    padding-top: 30px;
    padding-bottom: 40px; }

  &__content {
    display: flex;
    justify-content: space-between;
    @include r(1024) {
      flex-direction: column; }

    &-item {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      max-width: 475px;
      min-height: 500px;
      margin: 0 25px;
      @include r(1024) {
        max-width: 100%; }
      @include r(768) {
        min-height: 300px; }

      img {
        width: 100%;
        height: auto;
        margin-top: 5px; } }

    &-item:nth-child(2) {
      @include r(1024) {
        margin-top: 20px;
        img {
          order: 2; } } } } }
