.footer {
  min-height: 800px;
  padding-top: 70px;
  background-image: url(../img/footer-bg1.jpg);
  background-position: center;
  background-size: cover;
  color: white;
  @include r(768) {
    padding-top: 30px; }

  &-i {
    width: 100%;
    min-height: 800px; }

  &__title {
    margin-bottom: 25px;
    @include r(768) {
      margin-bottom: 10px; } }

  &__descr {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 50px;

    &-text {
      margin-bottom: 35px;
      text-align: center;
      @include r(768) {
        margin-bottom: 10px; }

      &--big {
        font-size: 20px;
        @include r(768) {
          font-size: 16px; } } } }

  &__btn {
    width: 460px;
    padding: 11px 75px;
    background-color: transparent;
    @include r(400) {
      width: 270px; }
    a {
      color: white; }
    @include r(768) {
      padding: 8px 40px; } }

  &__main {
    display: flex;
    justify-content: space-between;
    @include r(1024) {
      flex-direction: column;
      align-items: center; } } }


.contacts {
  @include r(1024) {
    order: 2;
    margin: 40px 0; }

  &__address {
    display: flex;
    align-items: flex-end;
    margin-bottom: 50px;
    @include r(768) {
      flex-direction: column;
      align-items: center; }

    &-logo {
      margin-right: 55px;
      @include r(768) {
        margin-right: 0;
        margin-bottom: 30px; } }

    &-item {
      margin-top: 30px;
      letter-spacing: 1px; }

    &-title {
      @include din700;
      color: $orange; } }

  &__communication {
    display: flex;
    flex-wrap: wrap;
    @include r(768) {
      flex-direction: column;
      align-items: center; } }

  &__coll {
    margin-right: 30px;
    @include r(768) {
      margin-right: 0; }

    &-phone {
      @include din700;
      font-size: 30px;
      line-height: 1;
      color: white;
      letter-spacing: 1.2px;
      @include r(1024) {
        font-size: 25px; } }

    &-email {
      font-size: 24px;
      color: $orange;
      @include r(1024) {
        font-size: 20px; } } } }

.socials {
  display: flex;
  width: 160px;
  justify-content: space-around;
  align-items: center;
  @include r(768) {
    margin-top: 20px; }

  &__item {

    &-link {
      svg {
        font-size: 45px; }
      &:hover {
        svg {
          fill: red;
          stroke: black; } } } } }

.form {
  width: 490px;
  @include r(1024) {
    width: 100%; }

  &__title {
    text-align-last: left; }

  &__in {}

  input, textarea {
    display: block;
    width: 100%;
    padding: 14px;
    margin-bottom: 5px;
    @include din300;
    font-size: 18px;
    color: #9f9f9f;
    font-style: italic;
    @include r(900) {
      width: 100%; }
    @include r(768) {
      min-width: 270px;
      font-size: 14px; } }

  textarea {
    max-width: 100%;
    min-height: 110px;
    margin-bottom: 30px; }

  &-i {
    display: flex;
    flex-direction: column;
    max-width: 490px; }

  &__btn {
    width: 180px;
    margin-left: auto; } }
