.out {
  overflow: hidden; }

.stock-link {
  position: fixed;
  top: 40px;
  left: 35px;
  z-index: 20;
  a {
    text-decoration: none; }
  @include r(1300) {
    top: 125px; }
  @include r(768) {
    top: 145px;
    left: 0; }

  &__link {
    display: flex;
    width: 80px;
    height: 80px;
    background-color: $red;
    border-radius: 50%;
    @include r(768) {
      width: 40px;
      height: 40px; } }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    margin: auto; }
  &__icon {
    font-size: 30px;
    line-height: 0;
    @include r(768) {
      font-size: 16px; } }
  &__text {
    font-size: 12px;
    color: white;
    text-transform: uppercase;
    text-decoration: none;
    @include r(768) {
      font-size: 8px; } } }


.phone-link {
  position: fixed;
  top: 65px;
  right: 85px;
  z-index: 20;
  @include r(1750) {
    top: 100px; }
  @include r(1024) {
    right: 40px; }
  @include r(768) {
    top: 145px;
    right: 0;
    img {
      width: 60px;
      height: 60px; } } }

.section-title {
  margin-bottom: 35px;
  @include din300;
  font-size: 30px;
  text-transform: uppercase;
  text-align: center;
  @include r(1024) {
    font-size: 24px; }
  &--white {
    color: white; }
  @include r(768) {
    margin-bottom: 15px;
    font-size: 20px; } }
