.hero {
  position: relative;
  display: flex;
  width: 100%;
  min-height: 950px;
  background: url('../img/hero-bg.jpg') no-repeat 90% top;
  background-size: cover;
  @include r(1024) {
    min-height: 600px; }
  @include r(768) {
    min-height: 400px; }

  &__content {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: 0 auto;
    @include r(768) {
      justify-content: center; }
    &::after {
      content: '';
      display: block;
      width: 26px;
      height: 22px;
      position: absolute;
      bottom: 65px;
      left: 50%;
      transform: translateX(-50%);
      background-image: url(../img/triangle.png);
      @include r(768) {
        bottom: 3px; } } }

  &__nav {
    margin-top: 90px;
    @include r(1600) {
      margin-top: 110px; }
    @include r(1500) {
      background-color: rgba(255, 255, 255, 0.7);
      border-radius: 10px;
      margin-bottom: 30px;
      padding: 10px; }
    @include r(1024) {
      margin-top: 30px; }
    @include r(768) {
      margin-top: 160px;
      margin-bottom: 30px; }

    &-logo {
      margin-bottom: 25px;
      @include r(1024) {
        width: 165px;
        @include r(768) {
          margin-bottom: 0; } } }

    &-list {
      display: flex;
      flex-direction: column;
      align-items: center;
      @include r(768) {
        display: none; } }

    &-item {
      width: 100%;
      text-align: center;
      &.is-active {
        width: 100%;
        background-color: $orange; } }

    &-link {
      display: block;
      width: 100%;
      padding: 7px;
      @include din300;
      font-size: 18px;
      text-decoration: none;
      text-transform: uppercase;
      @include r(1024) {
        font-size: 16px; } } }

  &__phone {
    margin-right: 90px;
    margin-top: 50px;
    @include r(1024) {
      margin-right: 0; }
    @include r(768) {
      display: none; }


    &-link {
      @include din700;
      font-size: 36px;
      text-decoration: none;
      color: $black;
      @include r(1024) {
        font-size: 30px; } } } }


.themes {
  position: absolute;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  @include r(768) {
    top: 80px; }
  &__item {
    padding: 0 20px;
    &:first-child {
      border-right: 2px solid $black; }
    &-link {
      display: flex;
      align-items: center; }
    svg {
      stroke: #2e2e2e;
      font-size: 50px; } } }
