.btn {
  display: inline-block;
  width: 100%;
  height: 100%;
  padding: 15px 30px;
  font-size: 20px;
  text-transform: uppercase;
  text-decoration: none;
  border: 1px solid lightgrey;
  background-color: $orange;
  a {
    text-transform: uppercase;
    text-decoration: none; }
  @include r(768) {
    padding: 10px 10px;
    font-size: 16px; }

  &--transparent {
    background-color: transparent; } }

.btn-close-popup {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 35px;
  height: 35px;
  background-color: transparent; }
