.advantages {
  min-height: 600px;
  padding: 65px 0 50px;
  background: $green url("../img/adv-bg.jpg") no-repeat 50% 50%;
  background-size: cover;
  @include r(1024) {
    min-height: 480px;
    padding: 40px 0; }
  @include r(768) {
    min-height: 610px; }

  &__container {
    width: 100%; } }

.tabs {

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 230px;
    margin-bottom: 50px;
    @include r(1024) {
      min-height: 100px; }
    @include r(768) {
      flex-wrap: wrap;
      min-height: 240px;
      justify-content: center;
      margin-bottom: 20px; } }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: 1px solid white;
    @include r(1024) {
      width: 50px;
      height: 50px; }
    @include r(768) {
     margin: 5px; }
    svg {
      font-size: 25px;
      fill: transparent;
      stroke: white;
      @include r(1024) {
        font-size: 18px; } }
    &.is-active {
      position: relative;
      width: 120px;
      height: 120px;
      background-color: $orange;
      border: none;
      @include r(768) {
        width: 80px;
        height: 80px; }
      svg {
        font-size: 60px;
        stroke: black;
        @include r(1024) {
          font-size: 50px; }
        @include r(768) {
          font-size: 40px; } }

      &::after {
        content: '';
        display: block;
        width: 26px;
        height: 22px;
        position: absolute;
        bottom: -50px;
        left: 50%;
        transform: translateX(-50%);
        background-image: url(../img/triangle.png);
        @include r(1024) {
          bottom: -35px; }
        @include r(768) {
          display: none; } } } }

  &__link {
    display: block;
    border-radius: 50%; }

  &__content-item {
    display: none;
    padding: 10px 20px;
    background-color: $orange;
    &.is-active {
      display: block; }
    @include r(768) {
      padding: 0; } }

  &__content-text {
    padding: 16px;
    font-size: 20px;
    line-height: 1.2;
    text-align: center;
    @include r(1024) {
      font-size: 16px; } } }
