.planning-popup {
  position: absolute;
  top: 500px;
  left: 0;
  z-index: 1;

  display: block;
  width: 1000px;
  min-height: 960px;
  background-color: white;

  border: 1px solid black;

  display: none;

  &.is-active {
    display: block; }

  &__in {
    position: relative; }

  & .room {

    &__img {
      display: flex;
      min-height: 475px;
      img {
        width: 50%; } } } }
